import { firstValueFrom } from 'rxjs'
import { CommonModule } from '@angular/common'
import { FormsModule } from '@angular/forms'
import {
  IonContent,
  IonHeader,
  IonTitle,
  IonList,
  IonItem,
  IonLabel,
  IonIcon,
  IonButton,
  IonText,
  IonCol,
  IonRow,
  IonSkeletonText,
  IonBadge,
  IonRouterOutlet,
  IonCard,
  IonCardHeader,
  IonCardSubtitle,
  IonCardContent,
  AlertController,
  LoadingController,
} from '@ionic/angular/standalone'
import { HeaderComponent } from 'src/app/components/header/header.component'
import {
  NotificationPreferencesService,
  RetrieveCyclist200Response,
  RetrieveProgram200ResponseConfigKycDocumentTypesInner,
} from 'src/api-cyclist'
import { addIcons } from 'ionicons'
import {
  idCardOutline,
  documentTextOutline,
  calendarNumberOutline,
  cardOutline,
  walletOutline,
  settingsOutline,
  alertCircleOutline,
  lockOpen,
  helpOutline,
  informationCircleOutline,
  shieldCheckmarkOutline,
  trashOutline,
  desktopOutline,
  bicycle,
} from 'ionicons/icons'
import { AuthService, AuthState } from 'src/app/services/external/auth.service'
import { RouterModule, Router } from '@angular/router'
import {
  RentingService,
  rentingState,
} from 'src/app/services/api-wrappers/renting.service'
import { TranslateModule, TranslateService } from '@ngx-translate/core'
import {
  AbonnementsState,
  AbonnementWrapperService,
  CurrentAbonnementState,
} from 'src/app/services/api-wrappers/abonnement.service'
import {
  PaymentMethodsService,
  paymentMethodsState,
} from 'src/app/services/api-wrappers/payment-method.service'
import {
  FactureService,
  FacturesState,
} from 'src/app/services/api-wrappers/facture.service'
import { NfcService, NfcState } from 'src/app/services/api-wrappers/nfc.service'
import { ModalController } from '@ionic/angular/standalone'
import { ForfaitListModalComponent } from 'src/app/components/modals/forfaits/forfaits.component'
import {
  AccountService,
  cyclistState,
} from 'src/app/services/api-wrappers/account.service'
import {
  CguService,
  cguLoadingState,
} from 'src/app/services/api-wrappers/cgu.service'
import {
  CookiesService,
  CoookiesState,
} from 'src/app/services/internal/cookies.service'
import { KYCWrapperService } from 'src/app/services/api-wrappers/kyc.service'
import { environment } from 'src/environments/environment'
import { ForfaitsWrapperService } from 'src/app/services/api-wrappers/forfaits.service'
import { FcmService } from 'src/app/services/external/fcm.service'
import { Capacitor } from '@capacitor/core'
import { StorageService } from 'src/app/services/utils/storage.service'
import { ErrorHandlerService } from 'src/app/services/utils/error-handler.service'

@Component({
  selector: 'app-menu',
  templateUrl: './menu.page.html',
  styleUrls: ['./menu.page.scss'],
  imports: [
    CommonModule,
    FormsModule,
    HeaderComponent,
    IonButton,
    IonCol,
    IonContent,
    IonIcon,
    IonItem,
    IonLabel,
    IonList,
    IonRow,
    IonSkeletonText,
    IonText,
    RouterModule,
    TranslateModule,
    IonCard,
    IonCardHeader,
    IonCardSubtitle,
    IonCardContent,
  ],
})
export class MenuPage {
  user?: RetrieveCyclist200Response
  tripState: rentingState
  aboState: AbonnementsState
  paymentMethodsState: paymentMethodsState
  nfcState: NfcState
  factureState: FacturesState
  cyclistState: cyclistState
  cguLoadingState: cguLoadingState
  cookiesState: CoookiesState
  termsState: {
    validated: boolean
    validated_at: number
  }
  currentAbonnementState: CurrentAbonnementState
  authState: AuthState = { state: 'SignedOut' }
  hasForfaitsAvailable: 'false' | 'true' = 'false'
  hasLldUrl: boolean = false
  subscriptionColor = computed(() => {
    if (
      this.currentAbonnementState.state === 'waiting_validation' &&
      this.accountService.hasBlockedReason(
        'Subscription does not allow renting'
      )
    ) {
      return 'warning'
    }
    return 'primary'
  })

  subscriptionExpirationColor = computed(() => {
    if (
      this.currentAbonnementState.state === 'active' &&
      this.AbonnementWrapperService.shouldShowExpirationWarning(
        this.currentAbonnementState.abonnement
      )
    ) {
      return 'primary'
    }
    return 'dark'
  })

  constructor(
    public authService: AuthService,
    private rentingService: RentingService,
    public paymentMethodsService: PaymentMethodsService,
    private factureService: FactureService,
    private nfcService: NfcService,
    public AbonnementWrapperService: AbonnementWrapperService,
    private modalCtrl: ModalController,
    private ionRouterOutlet: IonRouterOutlet,
    public accountService: AccountService,
    private cguService: CguService,
    private cookiesService: CookiesService,
    private notificationPreferencesService: NotificationPreferencesService,
    public kycService: KYCWrapperService,
    private forfaitsWrapperService: ForfaitsWrapperService,
    private alertController: AlertController,
    private loadingController: LoadingController,
    private fcmService: FcmService,
    private translate: TranslateService,
    private router: Router,
    private storage: StorageService,
    private errorHandler: ErrorHandlerService
  ) {
    addIcons({
      lockOpen,
      idCardOutline,
      calendarNumberOutline,
      informationCircleOutline,
      documentTextOutline,
      cardOutline,
      walletOutline,
      settingsOutline,
      helpOutline,
      alertCircleOutline,
      shieldCheckmarkOutline,
      trashOutline,
      desktopOutline,
      bicycle,
    })

    effect(() => {
      this.authState = this.authService.authState()
      if (this.authState.state === 'SignedIn') {
        this.notificationPreferencesService
          .listNotificationPreference({
            program: environment.program,
          })
          .toPromise()
      }
    })
    effect(() => {
      this.cyclistState = this.accountService.cyclistState()
    })
    effect(() => {
      this.currentAbonnementState =
        this.AbonnementWrapperService.currentAbonnementState()
    })
    effect(() => {
      this.paymentMethodsState =
        this.paymentMethodsService.paymentMethodsState()
    })
    effect(() => {
      this.cookiesState = this.cookiesService.cookiesState()
    })
    effect(() => {
      this.aboState = this.AbonnementWrapperService.listAbonnementsState()
    })

    this.rentingService.rentingState.subscribe((state) => {
      this.tripState = state
    })
    this.factureService.factureState.subscribe((state) => {
      this.factureState = state
    })
    this.nfcService.nfcState.subscribe((state) => {
      this.nfcState = state
    })
    this.cguService.cguLoadingState.subscribe((state) => {
      this.cguLoadingState = state
    })
  }

  async ngOnInit() {
    // Vérifier la disponibilité des forfaits
    this.hasForfaitsAvailable =
      (await this.forfaitsWrapperService.hasForfaits()) ? 'true' : 'false'

    // Vérifier si LLD URL est disponible
    const program = await this.storage.get('program')
    this.hasLldUrl = !!(
      program?.config?.lld_url && program.config.lld_url.trim() !== ''
    )
  }

  async logout() {
    await this.authService.signOut()
  }

  hasActiveTripOverThreeHours(): boolean {
    return this.rentingService.hasActiveTripOverThreeHours()
  }

  hasActiveTripOver24Hours(): boolean {
    return this.rentingService.hasActiveTripOver24Hours()
  }

  async openForfaitsModal(event: Event) {
    event.preventDefault()
    event.stopPropagation()

    const modal = await this.modalCtrl.create({
      component: ForfaitListModalComponent,
      componentProps: {
        next: 'menu',
      },
      presentingElement: this.ionRouterOutlet.nativeEl,
    })

    await modal.present()
  }

  // async openFeedbackModal() {
  //   if (
  //     this.cookiesState.state === 'already_set' &&
  //     this.cookiesState.preferences.analytics
  //   ) {
  //     this.sentryService.openFeedbackModal()
  //   } else {
  //     this.cookiesService.presentCookiesAlert()
  //   }
  // }

  isCookiesSet() {
    return this.cookiesService.cookiesState().state === 'already_set'
  }

  getDaysUntilExpiration(): number {
    const state = this.AbonnementWrapperService.currentAbonnementState()
    if (state.state === 'active' && state.abonnement.cancel_at) {
      return Math.floor(
        (state.abonnement.cancel_at * 1000 - Date.now()) / (1000 * 60 * 60 * 24)
      )
    }
    return 0
  }

  getExpirationPlural(): string {
    const days = this.getDaysUntilExpiration()
    return days > 1 ? 's' : ''
  }

  getForfaitName(): string {
    const state = this.AbonnementWrapperService.currentAbonnementState()
    if (state.state === 'active' || state.state === 'waiting_validation') {
      return state.abonnement.forfait.name
    }
    return ''
  }

  getPlanInterval(): string {
    const state = this.AbonnementWrapperService.currentAbonnementState()
    if (state.state === 'active' && state.abonnement.forfait.plan) {
      return state.abonnement.forfait.plan.interval
    }
    return ''
  }

  getPlanIntervalCount(): number {
    const state = this.AbonnementWrapperService.currentAbonnementState()
    if (state.state === 'active' && state.abonnement.forfait.plan) {
      return state.abonnement.forfait.plan.interval_count
    }
    return 0
  }

  async deleteAccount() {
    const alert = await this.alertController.create({
      header: this.translate.instant('account.deletion.header'),
      message: this.translate.instant('account.deletion.message'),
      buttons: [
        {
          text: this.translate.instant('generic.cancel'),
          role: 'cancel',
        },
        {
          text: this.translate.instant('account.deletion.confirm'),
          cssClass: 'text-danger',
          role: 'confirm',
          handler: async () => {
            const loading = await this.loadingController.create({
              message: this.translate.instant('account.deletion.loading'),
            })
            await loading.present()
            if (Capacitor.isNativePlatform()) {
              await this.fcmService.unRegisterPush()
            }
            try {
              await this.accountService.deleteAccount()
              await loading.dismiss()
              this.router.navigate(['/account'], { replaceUrl: true })
            } catch (error) {
              this.errorHandler.handleError(error)
            }
          },
        },
      ],
    })
    await alert.present()
  }
}
